p, span {
  font-family: 'Quicksand', sans-serif;
  font-size: 12px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Alegreya Sans SC';
}
.table{
  table-layout:fixed;
  border: none !important;
  padding: 4px;
}
.table th, table td {
  font-family: 'Quicksand', sans-serif;
  padding: 0 8px 3px 2px !important;
  font-size: 15px;
}
.table td:first-child {
  font-weight: bold;
  width: 165px;
}
.table td {
  border-top: none;
  border-bottom: 1px solid #dee2e6;
  max-width: 165px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.cust-dropdown {
  padding: 0px 2px 0 2px;
  border-radius: 0px;
  border-color: black;
  height: 38px !important;
  font-size: 18px;
}

.table th {
  border-right: 2px solid !important;
  margin: 0 0px 2px 0px !important
}

.indi-hor-card {
  padding: 12px;
  margin-top: 10px;
}
.indi-hor-card > .card-img > p {
  overflow-y: auto;
  height: 100px;
}
.indi-hor-card > .card-img > .card-title {
  width: 280px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.indi-hor-card > .card-footer {
  padding: 8px;
  margin-top: 8px;
  border: none;
}
.indi-hor-card > .card-footer > .card-link {
  font-size: 17px;
  cursor: pointer;
}
.indi-hor-card > .card-footer > .card-link:hover {
  font-weight: bold;
}
.cust-list{
  border-radius: 0px !important;
  background-color: #f9f9f9;
  padding: 3px 15px 3px 15px;
}
.trend-scroll {
  max-height: 490px;
  overflow-y: scroll;
}
.badge-custom {
  color: #fff;
  background-color: #e76229;
}
.trend-triple-dot {
  max-width: 130px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.climate-icon {
  position: absolute;
  right: 0px;
}
.weather-card-body{
  padding: 3px 50px 10px 3px;
}
.weather-card {
  border-radius: 0px;
  background-color: #d3d3d31c
}
.location-container {
  position: fixed;
}
.form-cust {
  display: flex
}
.cust-input {
  padding: 0px 2px 0 2px;
  border-radius: 0px;
  border-color: black;
}

.cust-btn {
  border-radius: 0px;
}
.input-h28 {
  height: 28px;
}
.btn-h28 {
  height: 28px;
  padding: 0px 8px 0px 8px;
}
.cust-table-cine {
  border-radius: 0px;
  background-color: #d3d3d31c
}

.search-box {
  margin: 10px 0 10px 0;
  padding: 16px;
  background-color: #e9ecef;
}
.sticky {
  position: fixed;
  top: 66px;
  width: 100%;
  z-index: 100
}
.container-fluid {
  margin-top: 90px;
}
.navbar-brand, .nav-container {
  display: flex;
  border-style: dashed;
  border-width: 1px;
  padding: 8px 10px 0px 10px;
  border-color: #000000;
  border-radius: 28px;
}
.navbar {
  background-color: #e86329!important;
  /* box-shadow: inset 1px 1px 20px 2px #fff;*/
}
.logo-title {
  font-family: 'Alegreya Sans SC';
  font-size: 28px;
  margin: 0px;
}
.navbar-text {
  font-family: 'Alegreya Sans SC';
  font-size: 24px;
}
.nav-a {
  font-size: 17px;
  color:black;
  font-family: 'Alegreya Sans SC';
  margin-right: 15px;
}

.nav-a:hover {
  text-decoration: underline;
  color: black;
  font-family: 'Alegreya Sans SC';
}

.add-to {
  position: absolute;
  right: 7px;
  top: 0px;
  background-color: #e76229;
  color: white;
  width: 16px;
  text-align: center;
}
.add-to:hover {
  cursor: pointer;
}
